import {Link} from 'gatsby';
import {StaticImage} from 'gatsby-plugin-image';
import React from 'react';

import Menu from './Menu';
import Button from './common/Button';
import {MenuOpen, RoundArrow} from './common/icons';

const HeaderItem = ({name, link, active}) => (
  <Link
    to={link}
    className={`flex items-center ${active ? 'text-n-100' : ''}`}
  >
    {active && <div className="bg-primary h-2 w-2 rounded-full mr-4" />}
    {name}
  </Link>
);

export default () => {
  const [isMenuOpened, setIsMenuOpened] = React.useState(false);

  return (
    <header className="container-large fixed z-50 inset-x-0">
      <div className="bg-n-500 py-4 px-10 flex justify-between items-center">
        <StaticImage
          src="../images/logo.png"
          alt="Elite Alliance"
          height={25}
          loading="eager"
          placeholder="none"
        />
        <div className="hidden lg:flex text-n-400 font-mono space-x-12">
          <HeaderItem
            name="About Us"
            link="/"
            active
          />
          <HeaderItem
            name="Contact"
            link="/"
          />
          <Button
            kind="primary"
            icon={<RoundArrow className="transform -rotate-90" />}
          >
            Sign In
          </Button>
        </div>
        <div className="flex lg:hidden items-center">
          <span className="hidden md:block font-mono text-n-100 uppercase mr-6">Menu</span>
          <button
            type="button"
            className="flex items-center"
            onClick={() => setIsMenuOpened(true)}
          >
            <MenuOpen className="text-n-100 w-14" />
          </button>
        </div>
      </div>
      <Menu
        isMenuOpened={isMenuOpened}
        setIsMenuOpened={setIsMenuOpened}
      />
    </header>
  );
};
